.womenone, .womentwo, .womenthird, .womenfourth, .womenfifth {
    height: 400px;
    width: 285px;
    margin-left: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;  
    padding: 10px;
    background-color: #1f1f4c

}

.womainmen{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 15px;
    justify-content: center;
    padding: 10px;
}
