.P_main{
    background-color: #E6E6FA;
    width: 100%;
    height: 600px;
    border-radius: 2px;
    padding-block: 1px;
    border-radius: 10px;
} 

.producttext{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

}

.productone, .producttwo, .productthird, .productfourth, .productfifth {
    height: 400px;
    width: 285px;
    margin-left: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;  
    padding: 10px;
    background-color: #1f1f4c

}
.productmain{
    display: flex;
    align-items: normal;
}
