.eletext{
    margin-top: 10px;
    font-size: 18px;
    color: #E6E6FA;
    text-align: center;

}

.price{
    color:#E6E6FA
}

.add-to-cart{
    margin-top: 10px;
    padding: 10px 20px;
    background-color: purple;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;

}
.add-to-cart:hover {
    background-color: rgb(95, 2, 95);
}

.productfourth:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}
