.mainfooter {
    background-color: #E6E6FA;
    border-radius: 10px;
    color: black;
    margin-top: 10px;
    padding: 10px;
}

.f, .ff {
    margin-left: 10px;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
}

.u {
    list-style: none;
}

.lowerf {
    background-color: #101036;
    border-radius: 10px;
    padding: 20px;
    margin-top: 20px;
}

.ffl {
    color: purple;
    margin-left: 10px;
    margin-bottom: 20px;
}

.footer-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}

.footer-col {
    flex: 1;
    min-width: 200px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: white;
}

.footer-col img {
    margin-right: 10px;
}

.address {
    font-size: large;
}

.locate {
    color: #add8e6;
    text-decoration: underline;
}

.rr {
    color: white;
    margin-top: 10px;
    display: flex;
    align-items: center;
    gap: 5px;
}
.logo-row {
    flex-direction: row !important;
    align-items: center;
    gap: 15px;
}
