.main {
  background-color: #E6E6FA;
  padding: 30px;
  border-radius: 10px;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
.header-container { 
    width: 125px;
  display: flex;
  align-items: center; 
  gap: 10px;
}
.title {
  color: purple;
  font-size: 28px;
  font-weight: bold;
  margin: 0;
}
.dropdown {
  padding: 8px;
  font-size: 16px;
  border: #E6E6FA;
  border-radius: 5px;
  cursor: pointer;
  background-color: #E6E6FA;
  font-weight: lighter;
}
.login{
  border-radius: 10px;
  cursor: pointer;
  background-color: #E6E6FA ;
  padding-top: 40px;
  font-size: 20px;
  gap:10px;
  align-items: left;
}
.login-button{
  background-color: #E6E6FA;
  border :0;
  cursor: pointer;
  margin-right:1px;
  height:22px;
  width:70px;
  margin-top: 16px;
}
.addtocart-button{
  background-color: #E6E6FA;
  cursor: pointer;
  border: 0;
  height:20px;
  width:10px;
  margin-right:40px 
}
.la{
  display: flex;
  justify-items: normal;
}
.img{
  background-color:#E6E6FA ;
  border: 0;
}
.input_class{
  height:50px;
  width:650px;
  background-color: aliceblue;
  display: flex;
  justify-items: center;
  align-content: center;
  align-items: center;
  border-radius: 15px;
  border-color: purple;
  cursor: pointer;
}
::placeholder{
  text-align: center;
  font-size: larger;
  font-style: oblique;
  justify-content: center;
  display: flex;
}
.cart-count{
  background-color: #E6E6FA;
  font-size: 30px;
  margin-bottom: 20px;
}
.al{
  background-color:#E6E6FA;
  border-radius: 10px;
  display: flex;
  padding-top: 7px;

}
.all{
  padding-left: 20px;
  margin-top: 10px;
}
.search_bar{
  padding-left: 250px;
}
.ratebutton{
  border-color:#E6E6FA;
  background-color:purple;
  cursor: pointer;
  height:30px;
  color:#E6E6FA
}
.rateus{
  padding-top: 10px;
}
