.m_main {
    background-color: #E6E6FA;
    width: 100%;
    padding: 20px 0;
    border-radius: 10px;
}

.maintextm {
    font-size: 20px;
    font-weight: bold;
    text-align: left;
    margin-left: 660px;
    margin-top: 10px;
    color:purple
}
.mainmen {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 15px;
    justify-content: center;
    padding: 10px;
}

.men-item {
    background-color: #3b185c; 
    border-radius: 10px;
    padding: 10px;
    text-align: center;
    /* transition: transform 0.2s ease-in-out; */
}

.men-item:hover {
    transform: scale(1.05);
}

.men-image {
    width: 100%;
    height: auto;
    max-height: 220px;
    border-radius: 8px;
    object-fit: cover;
}

.eletext {
    font-size: 14px;
    font-weight: 500;
    margin: 5px 0;
}

.price {
    color: #333;
    font-size: 14px;
}

.add-to-cart {
    margin-top: 8px;
    padding: 8px 15px;
    background-color: purple;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    transition: background 0.2s;
}

.add-to-cart:hover {
    background-color: rgb(95, 2, 95);
}
