.k_main{
    background-color: #E6E6FA;
    width: 100%;
    height: 600px;
    border-radius: 2px;
    padding-block: 1px;
    border-radius: 10px;

}

.maintextk{ 
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.kidone, .kidtwo, .kidthird, .kidfourth, .kidfifth {
    height: 400px;
    width: 285px;
    margin-left: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;  
    padding: 10px;
    background-color: #1f1f4c

}
