    margin-top: 10px;
    padding: 10px 20px;
    background-color: purple;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;

}
.add-to-cart:hover {
    background-color: rgb(95, 2, 95);
}

