.E_and_A_main {
    background-color: #E6E6FA;
    width: 100%;
    height: 500px;
    padding-block: 1px;
    border-radius: 10px;
    border-radius: 2px;
}

.elemaintext {
    margin-left: 20px;
    margin-top: 10px;
}
.mainele {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.eleone, .eletwo, .elethird, .elefourth, .elefifth {
    height: 400px;
    width: 285px;
    margin-left: 10px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;  
    padding: 10px;
    background-color: #1f1f4c

}
.eleone:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}
.eletwo:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}
.elethird:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}
.elefourth:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}
.elefifth:hover, .imgeo:hover {
    background-color: #101036;
    cursor: pointer;
}

.imgeo {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.eletext {
    margin-top: 10px;
    font-size: 18px;
    color: #E6E6FA;
    text-align: center;
}

.add-to-cart {
    margin-top: 10px;
    padding: 10px 20px;
    background-color: purple;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.add-to-cart:hover {
    background-color: rgb(95, 2, 95);
}
.price{
    color:#E6E6FA
} 
